<template>
	<div class="slide-container">
		<router-link to="/slide2" tag="button">下一页</router-link>
		<p>slide1</p>
		<button v-on:click="show = !show">show/hide text</button>
		<transition name="fade">
			<p v-if="show">Hello World!</p>
		</transition>
		<button @click="handleClick($event)" data-index="11">点击事件</button>
		<input type="text" class="bugText" @blur="bugText">
		<input type="text">
		<ul>
			<li v-for="(item,index) in arr" :key="index" v-myclick="item">{{item}}</li>
		</ul>
	</div>
</template>

<script>
	import {consoleKeybord} from '@/utils/common.js'
	export default {
		name: 'slide1',
		data() {
			return {
				show: true,
				domHeight:document.documentElement.clientHeight,
				arr:[1,2,3,4,5,6]			
			}
		},
		methods:{
			handleClick(e){
				console.log(e.target.dataset.index)
			},
			bugText(){
				console.log('键盘弹起后的高度',document.body.clientHeight)
			}
		},
		mounted() {
			console.log('页面初始高度',this.domHeight)
			// console.log(process.env.VUE_APP_BASE_API)
			consoleKeybord()
		},
		directives:{
			myclick:{
				bind:function(el,binding,vnode,oldVnode){
					el.addEventListener('click',function(){
						console.log(el,binding.value,vnode,oldVnode)
					})
				}
			}
		}
	}
</script>

<style lang="stylus" scoped>
	.slide-container {
		background-color: #eee;
		height: 100vh;
		// height:2000px;
		.bugText{
			position:absolute;
			bottom:50px;
			left:50px;
		}
	}

	.fade-enter-active,
	.fade-leave-active {
		transition: opacity 0.5s;
	}

	.fade-enter,
	.fade-leave-active {
		opacity: 0;
	}
</style>
